import React from "react"
import HeaderImg from "../HeaderImg/HeaderImg"
import {connect} from "react-redux"
import {Container,Col,Row} from "react-bootstrap"
import {deleteItem} from "../../../actions"
class CartComponent extends React.Component {
    
    
    render(){

        return (
        <>
            <HeaderImg section="Cart" title="My Cart" />
            <div className="cart-component">
                <Container>
                    <Row className=" cartNav mb-4 p-3 text-center">
                        <Col className="d-none d-lg-block" lg="1"></Col>
                        <Col className="d-none d-lg-block" lg="2"></Col>
                        <Col  xs="12" lg="3">Product</Col>
                        <Col className="d-none d-lg-block" lg="1">Price</Col>
                        <Col className="d-none d-lg-block" lg="3">Pieces</Col>
                        <Col className="d-none d-lg-block" lg="1">Total</Col>
                    </Row>
                   
                    {this.props.items.map(item => {
                        const {id} = item
                        return (
                            <Row key={id} className="px-md-5 px-lg-0 flex-column flex-lg-row align-items-center cart-item mb-4 p-3 text-center">
                                <Col className="mb-4" xs="12" lg="1"><button onClick={() => {console.log(id);this.props.deleteItem(id)}} className="close">&times;</button></Col>
                                <Col className="mb-4 px-md-5 px-lg-3" xs="12" lg="2"><img alt="cart" className="img-fluid" src={item.img} /></Col>
                                <Col className="mb-4 product-info text-center" xs="12" lg="5">
                                    <h4>{item.title}</h4>
                                    <p>{item.firstDesc}</p>
                                </Col>
                                <Col className="mb-4" xs="12" lg="1">{item.price}</Col>
                                <Col className="mb-4" xs="12" lg="1">{item.pieces}</Col>
                                <Col className="mb-4" xs="12" lg="1">${item.total}</Col>
                            </Row>
                        )
                    })}
                </Container>
            </div>
        </>
    )
    }
}


const getState = (state) => {
    return {items:state.data}
}

export default connect(getState,{deleteItem})(CartComponent)